

.contact{
    padding: 50px 0;
}

.contact-cards{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #006D77;
    border-radius: 10px;
}

.contact-right{
    position: relative;
}

.contact-img__one{
    position: absolute;
    top: 45%;
    right:280px;
    z-index: 2;
}

.contact-img__two{
    position: relative;
    top: 55px;
}

.contact-title{
    margin-bottom: 10px;
    font-size: 35px;
    color: aliceblue;
}

.contact-text{
    width: 28rem;
    line-height: 1.4;
    font-size: 15px;
    color: aliceblue;
}

.contact-card form{
    width: 250px;
    height: 40px;
    margin-top: 15px;
    padding:0 5px 0 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 20px;
    background-color: aliceblue;
}

.contact-card form input{
    width: 100%;
    height: 100%;
    border: none;
    outline: none;
    padding: 0 10px;
    font-size: 16px;
    border-radius: inherit;
    background-color: transparent;
}

.contact-card button{
    width:40px;
    height: 35px;
    border-radius: 50%;
    font-size: 19px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #006D77;
    border: none;
    color: aliceblue;
    cursor: pointer;
}

@media(max-width:992px){
    .contact-cards{
       flex-direction: column;
       align-items: center;
       text-align: center;
    }

    .contact-text, .contact-card form{
        width: 100%;
    }

    .contact-img__two{
        top: 10px;
    }
}

@media(max-width:776px){
    .contact-img__one, .footer-btn__one{
        display: none;
    }
}