

header{
    padding: 100px 0;
}

.header-cards{
    display: flex;
    justify-content: space-between;
}

.header-title{
    padding-bottom:15px;
    font-weight: 500;
    font-size: 18px;
    color: darkgray;
}

.header-subtitle{
    width: 380px;
    padding-bottom: 15px;
    font-size: 33px;
    font-weight: 500;
}

.header-info{
    width: 31rem;
    padding-bottom: 15px;
    font-size: 15px;
    line-height: 1.3;
    color: rgb(83, 82, 82);
}

.header-left__btns{
    padding: 20px 0 35px;
    display: flex;
    align-items: center;
    gap: 20px;
}

.header-left__btns button{
    padding: 10px;
    border: none;
    border-radius: 5px;
    display: flex;
    align-items: center;
    gap: 20px;
    cursor: pointer;
    transition: .3s linear;
}

.header-left__btns button:hover{
    background-color: darkcyan;
    color: aliceblue;
}

.header-btn__one{
    background-color: #15172e;
    color: aliceblue;
    font-weight: 500;
}

.header-btn__two{
    background-color: transparent;
}

.header-bottom__cards{
    display: flex;
    align-items: center;
    gap: 50px;
}

.header-bottom__card{
    display: flex;
    align-items: center;
    gap: 10px;
}

.header-icon{
    width: 50px;
    height: 50px;
    background-color: lightblue;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.header-bottom__info-num{
    font-weight: 500;
    font-size: 20px;
}

.header-bottom__info-text{
    font-size: 14px;
}

.header-right{
    position: relative;
}

.header-right__img-two{
    position: absolute;
    left: 80px;
    bottom: -115px;
}

@media(max-width:1200px){
    .header-right__img-two{
            bottom: -100px;
        }
        
        .category-subtitle, .category-heading{
            width: 100%;
        }
    
        .category-cards{
            justify-content: center;
            flex-wrap: wrap;
        }
    
        .plan-cards{
            column-gap: 50px;
            row-gap: 30px;
        }
    
        .plan-arrow{
            display: none;
        }
    }
    
    @media(max-width:992px){
        .header-cards{
            flex-wrap: wrap;
            gap: 35px;
            justify-content: center;
            text-align: center;
        }
    
        .header-subtitle{
            width: 100%;
        }
        
        .header-info{
            width: 100%;
        }
    
        .header-left__btns, .header-bottom__cards{
            justify-content: center;
        }
    
        .header-right__img-two{
            left: 25px;
            bottom: -110px;
        }
    
        .category-card{
            width: 100%;
        }
    
        .plan-cards{
            column-gap: 15px;
            row-gap: 10px;
        }
    
        .contact-cards{
           flex-direction: column;
           align-items: center;
           text-align: center;
        }
    
        .contact-text, .contact-card form{
            width: 100%;
        }
    
        .contact-img__two{
            top: 10px;
        }
    }
    
    @media(max-width:776px){
        .header-subtitle{
        font-size: 25px;
        }
    
        .heading{
            width: 100%;
            font-size: 28px;
        }
    
        .card-title{
            font-size: 23px;
        }
    
        .contact-img__one, .footer-btn__one{
            display: none;
        }
    
        .footer-cards{
           flex-direction: column;
           align-items: center;
           text-align: center;
        }
    
        .location{
            width: 100%;
        }
    
        .social{
            justify-content: center;
        }
    
        .footer-bottom{
            flex-wrap: wrap;
            gap: 5px;
        }
    
        .footer-card{
            padding: 5px 0;
        }
    }