@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&family=Rubik:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap');

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Rubik', sans-serif;
    text-decoration: none;
    list-style: none;
}

html{
    scroll-behavior: smooth;
}

html::-webkit-scrollbar{
    width: 10px;
    background-color: transparent;
}

html::-webkit-scrollbar-thumb{
    border-radius: 20px;
    background-color: rgb(0, 162, 255);
}

body{
    background-color: aliceblue;
}

.container{
    max-width: 1100px;
    width: 100%;
    margin: auto;
    padding: 10px;
}