.TopBtn {
  position: fixed;
  bottom: 4rem;
  display: none;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  right: 2rem;
  width: 45px;
  height: 45px;
  z-index: 9999;
  background-color: lightseagreen;
  cursor: pointer;
}
.TopBtn.active {
  display: flex;
}
.TopBtn svg {
  width: 20px;
  height: 20px;
  color: aliceblue;
}
.TopBtn:hover {
  background-color: rgb(10, 145, 138);
}

@media (max-width: 776px) {
  .heading {
    width: 100%;
    font-size: 28px;
  }
}
