.footer-top{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 35px 0;
}

.footer-top__right{
    display: flex;
    align-items: center;
    gap: 20px;
}

.footer-btn__one{
    border: none;
    font-weight: 500;
    background-color: transparent;
}

.footer-btn__two{
    border: none;
    font-weight: 500;
    padding: 7px 15px;
    border-radius: 35px;
    display: flex;
    align-items: center;
    gap: 10px;
    background-color: #111222;
    color: aliceblue;
    cursor: pointer;
}

.footer-btn__two span:nth-child(2){
    font-size: 20px;
}

.footer-cards{
    display: flex;
    justify-content: space-between;
    padding: 20px 0;
    border-top: 1px solid darkgray;
    border-bottom: 1px solid darkgray;
}

.footer-card{
    padding: 15px 0;
}

.footer-card__text{
    padding-bottom: 8px;
    font-weight: 500;
}

.location{
    width: 20rem;
    line-height: 1.3;
    margin: 8px 0;
}

.email a{
    margin: 8px 0;
    font-size: 14px;
    color: rgb(0, 153, 255);
}

.social{
    display: flex;
    margin: 8px 0;
    gap: 10px;
}

.social a{
    font-size: 19px;
    color: #15172e;
}

.footer-card li{
    line-height: 1.5;
}

.footer-card li a{
    font-size: 14px;
    color: rgb(124, 122, 122);
}

.footer-bottom{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 15px 0 5px;
    color: rgb(129, 127, 127);
}

.footer-bottom__right{
    display: flex;
    align-items: center;
    gap: 15px;
}

.site-links{
    font-size: 13px;
}



@media(max-width:776px){
    .heading{
        width: 100%;
        font-size: 28px;
    }

    .footer-cards{
       flex-direction: column;
       align-items: center;
       text-align: center;
    }

    .location{
        width: 100%;
    }

    .social{
        justify-content: center;
    }

    .footer-bottom{
        flex-wrap: wrap;
        gap: 5px;
    }

    .footer-card{
        padding: 5px 0;
    }
}