.products{
    padding: 50px 0 0;
}

.products-cards{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
    gap: 20px;
}

.products-card{
    display: flex;
    gap: 15px;
    height: 100px;
    padding: 20px 10px;
    border: 1px solid lightgray;
    border-radius: 5px;
}

.product-info__text{
    font-size: 15px;
    font-weight: 500;
    line-height: 1.1;
    padding-bottom: 8px;
}

.product-price{
    color: rgb(124, 122, 122);
}