.category{
    padding: 50px 10px;
}

.category-heading{
    width: 30rem;
    padding: 35px 0 20px;
}

.category-subtitle{
    width: 30rem;
    margin: auto;
    text-align: center;
    color: rgb(119, 117, 117);
}

.category-cards{
    display: flex;
    justify-content: space-between;
    padding: 50px 0;
    gap: 25px;
}

.category-card{
    width: 50%;
}

.category-left{
    padding: 20px 20px 10px;
    border: 1px solid rgba(169, 169, 169, 0.39);
    border-radius: 5px;
}

.card-title{
    margin-bottom: 5px;
    font-size: 25px;
    font-weight: 500;
}

.card-info{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 0;
    border-bottom: 1px  solid rgba(169, 169, 169, 0.39);
}

.card-info:last-child{
    border: none;
}

.card-info .card-text{
    font-size: 17px;
    color: rgb(124, 122, 122);
}

.card-num{
    padding: 8px;
    width: 35px;
    height: 35px;
    text-align: center;
    border-radius: 50%;
    color: #006D77;
    background-color: lightblue;
}

.category-right{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(210px, 1fr));
    column-gap: 25px;
    row-gap: 20px;
}

.category-card__child{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    height: 100px;
    padding: 0 8px;
    border: 1px solid rgba(169, 169, 169, 0.39);
    border-radius: 5px;
    cursor: pointer;
}

.bottom-link{
    margin-top: 50px;
    text-align: center;
}

.bottom-btn{
    padding: 8px 20px;
    background-color: rgb(253, 181, 193);
    color: rgb(247, 63, 207);
    border: none;
    border-radius: 20px;
    cursor: pointer;
}

@media(max-width:1200px){
    .category-subtitle, .category-heading{
        width: 100%;
    }

    .category-cards{
        justify-content: center;
        flex-wrap: wrap;
    }

    .category-card{
        width: 100%;
    }
}

@media(max-width:776px){
    .bottom-link{
        margin-top: 5px;
    }

    .card-title{
        font-size: 23px;
    }
}