.plan{
    position: relative;
}

.play-heading{
    padding-bottom: 35px;
    width: 32rem;
}

.plan-cards{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    column-gap: 110px;
    row-gap: 50px;
}

.plan-card{
    width: 285px;
    height: 280px;
    padding-top: 45px;
    border-radius: 10px;
    text-align: center;
}

.plan-card:nth-child(1){
    background-color:#FFF3EA;
}

.plan-card:nth-child(2){
    background-color:#FDE2E4;
}

.plan-card:nth-child(3){
    background-color: #DBECE5;
}

.plan-card:nth-child(4){
    background-color: #DBECE5;
}

.plan-card:nth-child(5){
    background-color: #D1ECFD;
}

.plan-text{
    font-size: 28px;
    font-weight: 500;
    margin: 15px 0 25px;
}

.plan-info{
    margin-bottom: 25px;
    color: rgb(112, 111, 111);
}

.arrow-one{
    position: absolute;
    top: 30%;
    left: 310px;
}

.arrow-two{
    position: absolute;
    top: 30%;
    right: 305px;
}

.arrow-three{
    position: absolute;
    top: 60%;
    right: 80px;
}

.arrow-four{
    position: absolute;
    bottom: 15%;
    right: 510px;
}

@media(max-width:1200px){
    .plan-cards{
        column-gap: 50px;
        row-gap: 30px;
    }

    .plan-arrow{
        display: none;
    }

    .plan-card{
        flex-grow: 1;
    }
}

@media(max-width:992px){
    .plan-cards{
        column-gap: 15px;
        row-gap: 10px;
    }

    .play-heading{
        width: 100%;
    }
}