.navbar{
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.247);
}

.navbar-cards{
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navbar-links ul{
    display: flex;
    align-items: center;
    gap: 35px;
}

.navbar-links ul li a{
    font-weight: 500;
    color: #111222;
}

.navbar-links ul li a:hover{
    color: rgb(180, 3, 3);
}

.navbar-links ul li a.active{
    color: blue;
}

.navbar-btn{
    padding: 8px 12px;
    font-size: 15px;
    border-radius: 30px;
    background-color: #111222;
    border: none;
    color: aliceblue;
    transition: .3s ease-in-out;
    cursor: pointer;
}

.navbar-btn:hover{
    background-color: #111222a2;
}

.burger-menu{
    display: none;
}

.navbar-right{
    display: flex;
    align-items: center;
    gap: 15px;
}
    
    @media(max-width:776px){
       .navbar-links{
        position: absolute;
        top: 70px;
        left: 0;
        right: 0;
        height: 0;
        background-color: rgb(224, 239, 252);
        overflow: hidden;
        transition: .3s linear;
    }
    
    .navbar-links.active{
        height: 205px;
    }
    
    .navbar-links ul{
        flex-direction: column;
        padding: 15px 0;
        gap: 20px;
        opacity: 0;
        transition: .4s linear;
       }
    
       .navbar-links.active ul{
        opacity: 1;
       }
    
       .burger-menu{
        display: block;
        cursor: pointer;
       }
    
       .burger-line{
        width: 30px;
        height: 6px;
        background-color: #111222;
        margin-bottom: 2px;
       }
    }