main{
    padding:90px 0 50px;
}

.heading{
    width: 22rem;
    margin: auto;
    font-size: 33px;
    font-weight: 500;
    text-align: center;
}

.main-cards{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 15px;
    padding: 35px 0 0;
}

.main-card{
    position: relative;
    padding: 15px;
    border:1px solid lightgray;
    border-radius: 10px;
    background: white;
    transition: .3s linear;
}

.main-card:hover{
    transform: scale(1.020);
    box-shadow:0 0 5px rgba(41, 41, 41, 0.411);
}

.main-card .card-img{
    height: 140px;
    text-align: center;
}

.card-img img{
    height: 100%;
}

.company{
    margin: 10px 0;
    color: rgb(141, 140, 140);
}

.product-info{
    font-weight: 500;
    line-height: 1.3;
    height: 50px;
    overflow-y: auto;
}

.product-info::-webkit-scrollbar{
    width: 5px;
    background-color: transparent;
}

.product-info::-webkit-scrollbar-thumb{
    background-color: transparent;
}

.product-rate{
    margin: 5px 0;
    display: flex;
    align-items: center;
    gap: 3px;
}

.product-rate svg{
    color: gold;
}

.card-bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.product-price{
    font-weight: 500;
    font-size: 15px;
    color: #006D77;
}

.card-bottom button{
    padding: 3px 10px;
    font-size: 12px;
    border-radius: 5px;
    background-color: lightblue;
    color: #006D77;
    border: none;
    cursor: pointer;
    transition: .2s linear;
}

.card-bottom button:hover{
    transform: scale(1.080);
    background-color: rgb(148, 221, 245);
}

.discount{
    position: absolute;
    top: 10px;
    left: 10px;
    padding: 3px 12px;
    font-size: 12px;
    border-radius: 20px;
    background-color: #111222;
    color: aliceblue;
}

@media(max-width:776px){
    .heading{
        width: 100%;
        font-size: 28px;
    }
}